* {
 margin:0px;
 padding:0px;
}

.container{
  height: 500px;
  width: 400px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
  border-style: solid;
  border-width: 1px;
  border-radius: 20px;
}

#userDiv, #numberDiv, #emailDiv, #confEmailDiv, #passDiv, #confPassDiv{
  position: absolute;
  left: 100px;
}


#userDiv{
  top: 60px;
}

#numberDiv{
  top: 110px;
}

#emailDiv{
  top: 160px;
}

#confEmailDiv{
  top: 210px;
}

#passDiv{
  top: 260px;
}

#confPassDiv{
  top: 310px;
}

#cancel{
  position: absolute;
  top: 420px;
  height: 30px;
  width: 205px;
  left: 100px;
  background-color: black;
  font-size: 15px;
  text-align: center;
  color: white;
  cursor: pointer;
}

#signUp{
  position: absolute;
  top: 380px;
  height: 30px;
  width: 205px;
  left: 100px;
  background-color: black;
  font-size: 15px;
  text-align: center;
  color: white;
  cursor: pointer;
}

.input {
  background-color: black;
  font-size: 15px;
  text-align: center;
  color: white;
  font-family: "Lucida Sans", "Lucida Sans";
}


